<template>
  <div class="custom-edit-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">网站防护</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/protection/' + domain }">防护配置</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/scan-attack-protection/' + domain }">扫描攻击防护</el-breadcrumb-item>
        <el-breadcrumb-item v-if="uuid == 'new'">新增</el-breadcrumb-item>
        <el-breadcrumb-item v-else>编辑</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-form class="custom-edit-form" :model="scanAttackProForm" :rules="rules" ref="scanAttackProForm"
          label-width="180px">
          <div>
            <el-form-item label="规则名称" prop="rule_name">
              <el-input v-if="uuid == 'new'" v-model="scanAttackProForm.rule_name"
                placeholder="请输入字母开头，字母或数字组合，仅支持_-两种符号"> </el-input>
              <el-input v-else v-model="scanAttackProForm.rule_name" disabled> </el-input>
            </el-form-item>
            <el-form-item label="规则详情">
              <el-input v-model="scanAttackProForm.rule_detail"> </el-input>
            </el-form-item>
            <el-form-item label="防护模块" class="is-required">
              <div class="match-box" v-for="(item, index) in moduleSmallMatchs" :key="index">
                <div class="match-box-content">
                  <div class="match_key_cascader">
                    <el-cascader separator=":" v-model="item.rule_match_key_list" :options="optionsModuleMatchKey"
                      :props="propsModuleMatchKey" @change="onChangeModuleMatchs($event, item)" clearable>
                    </el-cascader>
                  </div>
                </div>

                <el-button @click.prevent="removeModuleMatchs(item)">删除</el-button>
              </div>
              <el-button @click="addModuleMatchs()" plain type="primary" class="button-new">新增</el-button>
            </el-form-item>
            <el-form-item label="统计对象" class="is-required">
              <div class="match-box" v-for="(item, index) in ruleSmallMatchs" :key="index">
                <div class="match-box-content">
                  <div class="match_key_cascader">
                    <el-cascader separator=":" v-model="item.rule_match_key_list" :options="optionsMatchKey"
                      :props="propsMatchKey" @change="onChangeRuleMatchs($event, item)" clearable>
                    </el-cascader>
                  </div>
                  <div v-show="item.showInput" class="match_key_input">
                    <el-input v-model="item.rule_match_key" clearable
                      @change="onChangeRuleInput($event, item)"></el-input>
                  </div>
                </div>

                <el-button @click.prevent="removeRuleMatchs(item)">删除</el-button>
              </div>
              <el-button @click="addRuleMatchs()" plain type="primary" class="button-new">新增</el-button>
            </el-form-item>
            <el-form-item label="经过时间(秒)" prop="statics_time">
              <el-input v-model="scanAttackProForm.statics_time" placeholder="请输入大于0的数字">
              </el-input>
            </el-form-item>
            <el-form-item label="攻击次数超过" prop="statics_count">
              <el-input v-model="scanAttackProForm.statics_count" placeholder="请输入大于0的数字">
              </el-input>
            </el-form-item>


            <el-form-item label="IP处罚方式" prop="rule_action">
              <el-select v-model="scanAttackProForm.rule_action" placeholder="请选择" @change="onChangeRuleAction()">
                <el-option v-for="item in ruleAction" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>

            <el-form-item v-if="scanAttackProForm.rule_action == 'bot_check'">
              <el-select v-model="action_value" placeholder="请选择">
                <el-option v-for="item in optionsBotCheck" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
              <p class="form-info-color">
                说明：标准模式无需人机交互
              </p>
            </el-form-item>

            <el-form-item label="IP处罚时间" prop="block_time">
              <el-input v-model="scanAttackProForm.block_time" placeholder="请输入大于0的数字">
              </el-input>
            </el-form-item>
          </div>
        </el-form>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12">
            <a class="el-button el-button--primary is-plain" :href="'/#/scan-attack-protection/' + domain">返回</a>
          </el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickWebRuleProSubmit('scanAttackProForm')" :loading="loading">保存
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

  </div>
</template>
<script>
import { mixin, JXAjax, validateRuleName, validatePositiveInt } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
let DomainWeb = '';
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      domain:'',
      uuid: 'new',
      scanAttackProForm: {
        rule_detail: "",
        action_value: "",
        statics_time: "60",
        statics_count:"1000",
      },
      type: 'edit',
      optionsMatchKey: [
        {
          value: 'http_args',
          label: 'http_args',
          children: [
            { value: 'path', label: 'path', leaf:true },
            { value: 'query_string', label: 'query_string', leaf:true },
            { value: 'method', label: 'method', leaf: true },
            { value: 'src_ip', label: 'src_ip', leaf: true },
            { value: 'raw_body', label: 'raw_body', leaf: true },
            { value: 'version', label: 'version', leaf: true },
            { value: 'scheme', label: 'scheme', leaf: true },
            { value: 'raw_header', label: 'raw_header', leaf: true },
          ]
        },
        {
          value: 'header_args',
          label: 'header_args',
          children: [
            { value: 'host', label: 'host', leaf: true },
            { value: 'cookie', label: 'cookie', leaf: true },
            { value: 'referer', label: 'referer', leaf: true },
            { value: 'user_agent', label: 'user_agent', leaf: true },
            { value: 'default', label: '自定义', leaf: true },
          ]
        },
        {
          value: 'cookie_args',
          label: 'cookie_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'uri_args',
          label: 'uri_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'post_args',
          label: 'post_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'json_post_args',
          label: 'json_post_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
      ],
      optionsModuleMatchKey: [
        {
          value: 'web_engine_protection_result',
          label: 'Web防护引擎',
          children: [
            { value: 'sql_check', label: 'SQL注入防护', leaf: true },
            { value: 'xss_check', label: 'XSS防护', leaf: true },
            { value: 'cmd_exec_check', label: '命令执行防护', leaf: true },
            { value: 'code_exec_check', label: '代码执行防护', leaf: true },
            { value: 'webshell_update_check', label: 'WebShell上传防护', leaf: true },
            { value: 'sensitive_file_check', label: '敏感文件泄露防护', leaf: true },
            { value: 'path_traversal_check', label: '路径穿越防护', leaf: true },
            { value: 'high_nday_check', label: '高危Nday防护', leaf: true },
          ]
        },
        {
          value: 'web_rule_protection_result',
          label: 'Web防护规则',
        },
      ],
      
      moduleSmallMatchs: [{
        rule_match_key_list: [],
        rule_match_key: '',
        showInput: false
      }],
      ruleSmallMatchs: [{
        rule_match_key_list: [],
        rule_match_key: '',
        showInput: false
      }],
      
      ruleAction: [
        { value: "block", label: "阻断请求" },
        { value: "reject_response", label: "拒绝响应" },
        { value: "watch", label: "观察模式" },
        
      ],
      optionsBotCheck: [
        { value: "standard", label: "标准" },
        { value: "slipper", label: "滑块" },
        { value: "image", label: "图片验证码" },
      ],
      optionsDict: [],
      optionsNameList: [],
      custom_response: [],
      request_replace: [],
      response_replace: [],
      traffic_forward: [],
      action_value: "",
      propsMatchKey: {
        expandTrigger: 'hover',
      },
      propsModuleMatchKey: {
        expandTrigger: 'hover',
        lazy: true,
        lazyLoad(node, resolve) {
          if (node.value == "web_rule_protection_result") {
            var nodes = [];
            JXAjax(
              "post",
              "/waf/waf_get_web_rule_protection_list",
              { domain: DomainWeb },
              function (response) {
                var _data = response.data.message;
                if (_data.length > 0) {
                  _data.forEach(item => {
                    nodes.push({
                      label: item.rule_name,
                      value: item.rule_name,
                      leaf: true
                    })
                  });
                } else {
                  nodes.push({
                    label: "无",
                    value: "none",
                    leaf: true,
                    disabled: true
                  })
                }

                resolve(nodes)
              },
              function () {

              },
              "no-message"
            );
          }
        },
      },
    };
  },
  computed: {
    rules() {
      return {
        rule_name: [
          {
            required: true,
            message: '请输入规则名称',
            trigger: ["blur", "change"],
          },
          {
            validator: validateRuleName,
            trigger: ["blur", "change"],
          },
        ],
        action_value: [
          { 
            required: true, message: '请选择匹配方式', trigger: 'change' 
          }
        ],
        rule_action: [
          {
            required: true,
            message: '请选择IP处罚方式',
            trigger: 'change' 
          },
        ], 
        statics_time: [
          {
            required: true,
            message: '请输入大于0的数字',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePositiveInt,
            trigger: ["blur", "change"],
          },
        ],
        block_time: [
          {
            required: true,
            message: '请输入大于0的数字',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePositiveInt,
            trigger: ["blur", "change"],
          },
        ],
        statics_count: [
          {
            required: true,
            message: '请输入大于0的数字',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePositiveInt,
            trigger: ["blur", "change"],
          },
        ],
      }
    }
  },
  mounted() {
    var t = this;
    const route = useRoute()
    t.uuid = route.params.uuid;
    t.domain = route.params.domain;
    DomainWeb = t.domain;
    t.loadingPage = false
    if (t.uuid != "new") { 
      t.getData();
    }
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_scan_attack_protection";
      var oData = { domain: t.domain, rule_name: t.uuid };
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.scanAttackProForm = response.data.message;
          t.scanAttackProForm.rule_name = t.uuid;
          var _rule_matchs = JSON.parse(t.scanAttackProForm.statics_object)
          var _match = [];
          var _default = [
            "query_string",
            "json_post",
            "post",
            "cookie",
            "waf_description",
            "ctx",
            "string"
          ]

          for (var j in _rule_matchs) {
            var item = _rule_matchs[j]
            var key = item.key
            var value = item.value
            var show = "false"
            if (_default.indexOf(key) > -1) {
              show = "true"
            }

            _match.push({
              "rule_match_key_list": [key, value],
              "rule_match_key": key + ':' + value,
              "showInput": show
            })
          }
          t.ruleSmallMatchs = _match;

          //防护模块
          var _rule_module = JSON.parse(t.scanAttackProForm.rule_module)
          var module = [];
          for (var i in _rule_module) {
            var item_module = _rule_module[i]
            var key_module = item_module.key
            var value_module = item_module.value
            var show_module = "false"

            module.push({
              "rule_match_key_list": [key_module, value_module],
              "rule_match_key": key_module + ':' + value_module,
              "showInput": show_module
            })
          }
          t.moduleSmallMatchs = module;
          
          t.action_value = t.scanAttackProForm.action_value;
         },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
      
    },
    onChangeRuleAction() {
      var t = this;
      t.action_value = "";
    },
    onClickWebRuleProSubmit(scanAttackProForm) {
      var t = this;      
      var _match = [];
      var _module = [];
      if (t.ruleSmallMatchs.length == 0) {
        t.$message({
          message: '请选择统计对象',
          type: "error",
        });
        return false;
      }
      if (t.ruleSmallMatchs[0].rule_match_key == "") {
        t.$message({
          message: '请选择统计对象',
          type: "error",
        });
        return false;
      }
      if (t.moduleSmallMatchs.length == 0) {
        t.$message({
          message: '请选择防护模块',
          type: "error",
        });
        return false;
      }
      if (t.moduleSmallMatchs[0].rule_match_key == "") {
        t.$message({
          message: '请选择防护模块',
          type: "error",
        });
        return false;
      }

      for (var j in t.ruleSmallMatchs) {
        var item = t.ruleSmallMatchs[j]
        var _str = "";
        var _arr = [];
        var _key = "";
        var _value = "";

        if (item.rule_match_key) {
          _arr = item.rule_match_key.split(":");
        }
        if (_arr.length > 0) {
          _key = _arr[0];
          _value = item.rule_match_key.replace(new RegExp(_key + ":"), "");
          _str = '{"key":"' + _key + '" , "value":"' + _value + '"}'
        }
        _match.push(JSON.parse(_str))
      }

      for (var i in t.moduleSmallMatchs) {
        var item_module = t.moduleSmallMatchs[i]
        var _str_module = "";
        var _arr_module = [];
        var _key_module = "";
        var _value_module = "";

        if (item_module.rule_match_key) {
          _arr_module = item_module.rule_match_key.split(":");
        }
        if (_arr_module.length > 0) {
          _key_module = _arr_module[0];
          _value_module = item_module.rule_match_key.replace(new RegExp(_key_module + ":"), "");
          _str_module = '{"key":"' + _key_module + '" , "value":"' + _value_module + '"}'
        }
        _module.push(JSON.parse(_str_module))
      }

      if (t.scanAttackProForm.rule_action == "bot_check" && t.action_value == "") {
        t.$message({
          message: '请选择人机识别方式',
          type: "error",
        });
        return false;
      }
      t.scanAttackProForm.domain = t.domain;
      t.scanAttackProForm.action_value = t.action_value;
      var postUrl = "/waf/waf_edit_scan_attack_protection";

      if (t.uuid == 'new') {
        postUrl = "/waf/waf_create_scan_attack_protection";
      } else {
        t.scanAttackProForm.rule_name = t.uuid;
      }
      t.scanAttackProForm.statics_object = JSON.stringify(_match);
      t.scanAttackProForm.rule_module = JSON.stringify(_module);
      
      this.$refs[scanAttackProForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.scanAttackProForm,
            function (response) {
              t.loading = false;
              window.location.href = '/#/scan-attack-protection/' + t.domain;
              
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    
    removeRuleMatchs(item) {
      var index = this.ruleSmallMatchs.indexOf(item);
      if (index !== -1) {
        this.ruleSmallMatchs.splice(index, 1);
      }
    },
    addRuleMatchs() {
      this.ruleSmallMatchs.push({ rule_match_key: '', showInput: false })
    },
    onChangeRuleMatchs(event, item) {
      if (event && event.length > 0) {
        var index = this.ruleSmallMatchs.indexOf(item);
        if (event[1] == 'default') {
          this.ruleSmallMatchs[index].showInput = true;
          this.ruleSmallMatchs[index].rule_match_key = event[0] + ':';
        } else {
          this.ruleSmallMatchs[index].rule_match_key = event[0] + ':' + event[1];
        }
      }

    },
    onChangeRuleInput(event, item) {
      var index = this.ruleSmallMatchs.indexOf(item);
      if (event == '') {
        this.ruleSmallMatchs[index].showInput = false;
        this.ruleSmallMatchs[index].rule_match_key = "";
        this.ruleSmallMatchs[index].rule_match_key_list = [];
      } else {
        this.ruleSmallMatchs[index].rule_match_key = event;
      }
    },

    removeModuleMatchs(item) {
      var index = this.moduleSmallMatchs.indexOf(item);
      if (index !== -1) {
        this.moduleSmallMatchs.splice(index, 1);
      }
    },
    addModuleMatchs() {
      this.moduleSmallMatchs.push({ rule_match_key: '', showInput: false })
    },
    onChangeModuleMatchs(event, item) {
      if (event && event.length > 0) {
        var index = this.moduleSmallMatchs.indexOf(item);
        if (event[1] == 'default') {
          this.moduleSmallMatchs[index].showInput = true;
          this.moduleSmallMatchs[index].rule_match_key = event[0] + ':';
        } else {
          this.moduleSmallMatchs[index].rule_match_key = event[0] + ':' + event[1];
        }
      }

    },
  },
};
</script>
<style>
.custom-edit-wrap {
  max-width: 800px;
  min-width: 400px;
}
.custom-edit-wrap .match-inline-block {
  width: 192px;
}
.custom-edit-form .el-checkbox + .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-select {
  width: 100%;
}
.custom-edit-form .match-box {
  
  display: inline-block;
  margin-bottom: 10px;
}
.custom-edit-form .el-button {
  margin-left: 10px;
}

.custom-edit-form .match-box-content {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_cascader {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_input {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  width: 100%;
}
.custom-edit-form .rule-level-box .el-form-item__content {
  margin-left: 10px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
.box-card-rule .card-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.box-card-rule .card-item-bottom {
  text-align: right;
  margin-bottom: 20px;
}
</style>
